import {
    Component
} from "react"
import {
    Link
} from "react-router-dom"
import prettyBytes from 'pretty-bytes'
import FadeIn from '../Fade'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import ReactLoading from 'react-loading'

class ServerRow extends Component {
    render() {
        return (
            <FadeIn>
            <Link to={`server/${this.props.id}`} style={{ textDecoration: 'none' }}>
                <div class="p-4 mb-3 server-row" style={{backgroundColor: 'rgb(50, 50, 50)'}}>
                <div class="d-flex justify-content-between" style={{alignItems: 'center'}}>
                    <div className="col-md-4 col-sm-3 ">
                        <div className="d-flex">
                        {this.props.current_state == "running" ? <FontAwesomeIcon className="mt-2 me-2" icon={faCircle} color={'green'}/>: ""}
                        {this.props.current_state == "offline" ? <FontAwesomeIcon className="mt-2 me-2" icon={faCircle} color={'red'}/>: ""}
                        {this.props.current_state == "starting" ? <FontAwesomeIcon className="mt-2 me-2" icon={faCircle} color={'yellow'}/>: ""}
                        {this.props.current_state == "stopping" ? <FontAwesomeIcon className="mt-2 me-2" icon={faCircle} color={'orange'}/>: ""}
                        {this.props.current_state == "loading" ? <ReactLoading className="me-2" color="#EC635D" type='spin' height={20} width={20} /> : ""}
                        <h3>{this.props.name}</h3>
                        </div>
                        {this.props.description ? 
                        <p>{this.props.description}</p> :
                        ""}
                    </div>
                    <div className="col-md-2 d-none d-sm-block">
                        <div className="d-flex justify-content-end">
                            <p>{this.props.current_disk}</p>
                            <p>/{this.props.disk}</p>
                        </div>
                    </div>
                    <div className="col-md-2 d-none d-sm-block">
                        <div className="d-flex justify-content-end">
                            <p>{this.props.current_memory}</p>
                            <p>/{this.props.memory}</p>
                        </div>
                    </div>
                </div>
                </div>
            </Link>
            </FadeIn>
        )
    }
}
export default ServerRow;